body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* PAGE */

html {
   line-height: 1.5;
}

body {
  font-size: 1em;
  font-family: Cairo,Arial,sans-serif;
  font-weight: 300;
  letter-spacing: 0;
}

h1, h2, h3 {
  font-family: 'Oswald', 'Arial Narrow', sans-serif;
}

code {
  display: block;
  margin: 20px 0 15px 0;
  padding: 10px;
  background: #eee;
}

.App_market_container {

}

.container {
  box-sizing: border-box;
  width: 100%;
  padding: 45px 20px;
}

.pure-button-primary {
  background-color: #0c1a2b;
}

.pure-button-primary:hover {
  background-color: #233e5e;
}

.pure-form input[type="text"]:focus {
  border-color: #0c1a2b;
}

/* NAVBAR */

.navbar {
  position: fixed;
  padding: 5px;
  background: #0c1a2b;
  font-family: 'Oswald', 'Arial Narrow', sans-serif;
}

.navbar a {
  color: #fff;
}

.navbar a:active,
.navbar a:focus,
.navbar a:hover {
  background: #233e5e;
}

.navbar .pure-menu-heading {
  font-weight: bold;
  text-transform: none;
}

.navbar .navbar-right {
  float: right;
}

.navbar .uport-logo {
  height: 16px;
  margin-right: 10px;
}

#App-loading-screen {
  background-color: #202930;
  height: 100vh;
  width: 100vw;
  color: #9aa3ad;
  font-size: 2rem;
  line-height: 2rem;
  font-weight: 600;
  text-align: center;
  padding-top: 30%;
}
.OrderList {
	background-color: #152128;
	width: 100%;
	height: 400px;
	overflow-y: auto;
}

#OrderList-table {
	width: 100%;
	margin: 0;
}

.OrderList-table-entry {
	color: #9aa3ad !important;
}

.OrderList-table-row {
	cursor: pointer;
}

.OrderList-table-header {
	color: #9aa3ad !important;
	background-color: #0b1216 !important;
	font-size: 1.2rem;
}

.OrderList-loading {
	color: #9aa3ad;
	font-size: 2rem;
	height: 100%;
	width: 100%;
	margin: auto 0;
	text-align: center;
	padding-top: 50%;
}
.BuySell {
	background-color: #152128;
	width: 100%;
	padding: 1.5rem;
}

.BuySell-headers {
	font-size: 1.15rem;
	font-weight: 600;
	line-height: 2rem;
}

.BuySell-button {
	margin-right: 1rem !important;
}
.MyOrders {
	background-color: #152128;
	width: 100%;
	height: 250px;
	overflow-y: auto;
}

.MyOrders-headers {
	font-size: 1.15rem;
	font-weight: 600;
	line-height: 2rem;
}

.MyOrders-button {
	margin-right: 1rem !important;
}

.MyOrders-table-entry {
	color: #9aa3ad !important;
}

.MyOrders-table-header {
	color: #9aa3ad !important;
	background-color: #0b1216 !important;
	font-size: 1.2rem;
}

#MyOrders-empty {
	color: #9aa3ad !important;
	opacity: 0.5;
	font-size: 1.15rem;
	font-weight: 600;
	text-align: center;
	width: 100%;
	height: 100px;
	padding-top: 25px;
}
.MarketHistory {
	background-color: #152128;
	width: 100%;
	height: 250px;
	overflow-y: auto;
}

.MarketHistory-headers {
	font-size: 1.15rem;
	font-weight: 600;
	line-height: 2rem;
}

.MarketHistory-button {
	margin-right: 1rem !important;
}

.MarketHistory-table-entry {
	color: #9aa3ad !important;
}

.MarketHistory-table-header {
	color: #9aa3ad !important;
	background-color: #0b1216 !important;
	font-size: 1.2rem;
}

#MarketHistory-empty {
	color: #9aa3ad !important;
	opacity: 0.5;
	font-size: 1.15rem;
	font-weight: 600;
	text-align: center;
	width: 100%;
	height: 100px;
	padding-top: 25px;
}

.MarketHistory-type {
	font-weight: 600;
}

.ui.table thead tr:first-child > th {
     position: -webkit-sticky !important;
     position: sticky !important;
     top: 0;
     z-index: 2;
}
.MyHistory {
	background-color: #0b1216;
	width: 100%;
	height: 250px;
}
#Side_bar {
	color: #202930;
	width: 27.5rem;
	background-color: #ced0d2;
	padding-left: 2rem;
	padding-top: 1rem;
}

#Side_bar-title {
	font-size: 3rem;
	font-weight: 500;
	line-height: 2rem;
	padding-bottom: 4rem;
}

#Side_bar-subtitle {
	font-size: 1.35rem;
	padding-bottom: 1rem;
}

#Side_bar-x {
	cursor: pointer;
}

#Side_bar-x-container {
	width: 100%;
	text-align: right;
	padding-bottom: 1rem;
}

#Side_bar-info {
	padding-bottom: 5rem;
}

.Side_bar-info-item {
	padding-bottom: 1rem;
}

.Side_bar-info-title {
	font-size: 1.1rem;
	font-weight: 500;
}

.Side_bar-info-content {
	font-size: 1.5rem;
	font-weight: 600;
}

#Side_bar-exchange {
	font-size: 1.15rem;
	font-weight: 600;
}

#Side_bar-mini-buttons {
	font-size: 0.5em;
	margin-bottom: 1rem;
}

#Side_bar-form {
	padding-bottom: 1rem;
}
.Market {
	background-color: #202930;
	color: #9aa3ad;
	min-height: 100vh;
	height: 100%;
	width: 100%;
	padding: 3rem 30.5rem 3rem 8rem;
}

.Market-h1 {
	font-size: 2.5rem;
	line-height: 0.8;
	letter-spacing: .1px;
	font-weight: 300;
	margin: 0;
}

#Market-title {
	padding-bottom: 2rem;
}

#Market-chart {
	padding-bottom: 2rem;
}

#Market-buysell {
	padding-bottom: 2rem;
}

#Market-activity-pane {
	padding-bottom: 2rem;
}

#Market-orderlists {
}

.Market-headers {
	font-size: 1.5rem;
	font-weight: 600;
	line-height: 2rem;
	padding: 0.25rem;
}

.Market-tab-pane {
	padding: 0 !important;
	border: 0 !important;
}

.red {
	color: red;
}

.green {
	color: green;
}

.ui.tabular.menu {
	border: 0px !important;
}

.ui.tabular.menu .item {
	color: white;
	background: #152128;
}

.ui.tabular.menu .item:hover {
	color: white;
	background: #111a21;
}

.ui.tabular.menu .active.item {
	background: #0b1215;
	color: white;
	border: 0;
	border-radius: 0 !important;
}
#Home {
	background-color: #202930;
	color: #9aa3ad;
	min-height: 100vh;
	height: 100%;
	width: 100%;
	padding: 10rem 30.5rem 3rem 8rem;
}

#Home-logo {
	background-color: white;
}

#Home-title {
	font-size: 5rem;
	line-height: 5rem;
	font-weight: 600;
	width: 100%;
	text-align: center;
}

#Home-subtitle {
	font-weight: 500;
	font-size: 2rem;
	line-height: 2rem;
	padding-top: 2rem;
	width: 100%;
	text-align: center;
}
#Navbar {
	background-color: #0b1216;
	width: 5rem;
	height: 100vh;
	padding: 0;
	margin: 0;
	position: fixed;
	left: 0;
	top: 0;
}

#Navbar-logo {
	background-color: white;
}

.Navbar-item {
	padding: 0 !important;
	text-align: center;
}

.Navbar-icon {
	width: 100%;
}

.Navbar-button {
	padding: 0.5rem !important;
	width: 100%;
	height: 5rem;
	color: white;
	background-color: #152128;
	display: flex;
	align-items: center;
}
#Infobar {
	background-color: #152128;
	color: #9aa3ad;
	width: 27.5rem;
	min-height: 100vh;
	height: 100%;
	padding: 0;
	margin: 0;
	position: fixed;
	right: 0;
	top: 0;
}

.Infobar-header {
	padding: 2rem 0rem;
	width: 100%;
	text-align: center;
	font-weight: 600;
	font-size: 1rem;
}

.Infobar-item {
	padding: 2rem;
}

.Infobar-table-entry {
	color: #9aa3ad !important;
}

#Infobar-table-header {
	background-color: #0b1215;
}

.ui.basic.striped.table tbody tr:nth-child(2n) {
	background-color: #111a21eb !important;
}

#Infobar-table {
	margin-top: 0px !important;
}
.WrapStation {
	background-color: #111a21eb;
	width: 100%;
	margin-top: 3em;
	height: 350px;
	overflow-y: auto;
}

.WrapStation-loading {
	color: #0b1216;
	font-size: 2rem;
	height: 100%;
	width: 100%;
	margin: auto 0;
	text-align: center;
	padding-top: 50%;
}

.WrapStation-headers {
	font-size: 1.15rem;
	font-weight: 600;
	line-height: 2rem;
	padding-top: 2rem;
}

.WrapStation-mini-buttons {
	font-size: 0.5em;
}

#WrapStation-title {
	font-weight: 600;
	font-size: 1.6rem;
	line-height: 1.1em;
	width: 100%;
	padding: 1rem;
	background-color: #0b1216;
}

#WrapStation-form {
	padding: 0 1rem;
	background-color: #111a21eb;
}
